// reviews style
.reviews { 
  background: #fff;
  
  padding-top: 60px;    
  padding-bottom: 60px;
  
}

.reviews__item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.reviews__img {
  width: 85px;
  height: 85px;
  text-align: center;
  margin-bottom: 16px;
}

.reviews__text {
  position: relative;

  background-color:  #fafafa;
  background-image: url('../images/reviews/quotes.svg'), 
  repeating-linear-gradient(
    to bottom,
    #fafafa,
    #fafafa 76px,
    #e1e1e1 76px,
    #e1e1e1 78px,
    #fafafa 78px,
    #fafafa 108px,
    #e1e1e1 108px,
    #e1e1e1 110px,
    #fafafa 110px,
    #fafafa 138px);
          ;
  background-repeat: no-repeat;
  background-position: top 32px left 65px, top 0px left 72px;
  background-size: auto, 822px 138px;


  max-width: 968px;
  padding: 47px 109px 39px 109px;
  margin-bottom: 36px;

  font-family: DM Sans;
  font-size: 16px;
  font-weight: 500;
  line-height: 30px;
  letter-spacing: 0.04em;
  text-align: center;
  color: #000000;
}

