.reviews__person {
  position: relative;
  font-family: DM Sans;
  font-size: 20px;
  font-weight: 500;
  line-height: 37px;
  letter-spacing: 0em;
  text-align: center;
  color: #000000;
}

.reviews__person::before {
  position: absolute;
  display: inline-block;
  content: "";

  top: -16px;
  left: 40%;

  width: 32px;
  height: 4px;
  background-image: url('../images/reviews/points.svg');
  background-repeat: no-repeat;
  background-position: 50%;
}
