.products {
    background-color: $primaryTextColor;
    padding-top: 60px;
    padding-bottom: 60px;
};

.products__subtitle {
    @include fontstyle (400, 18px, 1.17);
    font-family: 'Titan One', cursive;
    text-transform: uppercase;
    color: $subtitleTextColor;;

    text-align: center;
    margin-bottom: 15px;

    @include mq(tablet) {
    @include fontstyle (400, 26px, 1.15);
    }

    @include mq(desktop) {
    @include fontstyle (400, 30px, 1.15);
    }
};

.products__title {
    @include fontstyle (400, 34px, 1.15);
    font-family: 'Titan One', cursive;
    text-transform: uppercase;
    color: $secondaryTextColor;

    text-align: center;
    margin-bottom: 148px;

    @include mq(tablet) {
    @include fontstyle (400, 48px, 1.15);
    }

    @include mq(desktop) {
    @include fontstyle (400, 58px, 1.15);
    letter-spacing: 0.06em;
    margin-bottom: 280px;
    }
};

.products-list {
    margin: 0 auto;
    max-width: 480px;
    @include mq(tablet) {
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    }
};

.products-list__item {
    padding: 160px 40px 40px;
    border-radius: 24px;
    background-position: center;
    background-size: contain;

    @include mq(mobile-only) {
        margin-bottom: 140px;
        &:last-child {
        margin-bottom: 0;
        }
    }
    @include mq(tablet) {
        flex-basis: calc((100% - 32px) / 3);
        padding: 118px 32px 32px;
    }

    @include mq(desktop) {
        flex-basis: calc((100% - 64px) / 3);
        padding: 165px 62px 60px;
    }
};

.products-list__item--ice-cream {
    background-color: $headerBgc;
}


.products-list__item--ice-coffee {
    background-color: $iceCoffeeBgc;
}

.products-list__item--milkshakes {
    background-color: $milkShakesBgc;
}

    

.products-list__title {
    position: relative;
    @include fontstyle (400, 20px, 1.15);
    text-transform: uppercase;
    color: $primaryTextColor;
    
    text-align: center;
    margin-bottom: 70px;
    font-family: 'Titan One', cursive;

    @include mq(desktop) {
    @include fontstyle (400, 30px, 1.15)
    }
};
.products-list__title::before {
                display: block;
                content: "";
                position: absolute;
                bottom: -97px;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 280px;
                height: 280px;
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
        
                @include mq(tablet) {
                    width: 224px;
                    height: 224px;
                    bottom: -58px;
                }
        
                @include mq(desktop) {
                    width: 368px;
                    height: 394px;
                    bottom: -130px;
                }

}
.products-list__title--ice-cream {
    &::before {
            background-image: url(../images/products/product-1_sm.png);
    
            @include mq(retina) {
                background-image: url(../images/products/product-1_sm@2x.png);
            }
    
            @include mq(tablet) {
                background-image: url(../images/products/product-1_md.png);
    
                @include mq(retina) {
                    background-image: url(../images/products/product-1_md@2x.png);
                }
            }
    
            @include mq(desktop) {
                background-image: url(../images/products/product-1_lg.png);
    
                @include mq(retina) {
                    background-image: url(../images/products/product-1_lg@2x.png);
                }
            }
        }
}
.products-list__title--ice-coffee {
    &::before {
    background-image: url(../images/products/product-2_sm.png);
    
        @include mq(retina) {
            background-image: url(../images/products/product-2_sm@2x.png);
        }
    
        @include mq(tablet) {
            background-image: url(../images/products/product-2_md.png);
    
            @include mq(retina) {
                background-image: url(../images/products/product-2_md@2x.png);
            }
        }
    
        @include mq(desktop) {
            background-image: url(../images/products/product-2_lg.png);
    
            @include mq(retina) {
                background-image: url(../images/products/product-2_lg@2x.png);
            }
        }
    }
}
.products-list__title--milkshakes {
    &::before {
            background-image: url(../images/products/product-3_sm.png);
    
            @include mq(retina) {
                background-image: url(../images/products/product-3_sm@2x.png);
            }
    
            @include mq(tablet) {
                background-image: url(../images/products/product-3_md.png);
    
                @include mq(retina) {
                    background-image: url(../images/products/product-3_md@2x.png);
                }
            }
    
            @include mq(desktop) {
                background-image: url(../images/products/product-3_lg.png);
    
                @include mq(retina) {
                    background-image: url(../images/products/product-3_lg@2x.png);
                }
            }
        }
}

.products-list__title::after {
    position: absolute;
    display: block;
    content: '';
    width: 6px;
    height: 6px;
    border-radius: 50%;
    top: 58px;
    left: 50%;
    transform: translateX(-50%);
    background-color: $primaryTextColor;
    box-shadow: $primaryTextColor 10px 0px,
        $primaryTextColor -10px 0px;
    
    @include mq(desktop) {
        top: 65px;
    }
}
.products-list__description {
    @include fontstyle (700, 14px, 1.5);
    color: $primaryTextColor;
    
    text-align: center;
    margin-bottom: 32px;
    font-family: 'DM Sans', sans-serif;

    @include mq(desktop) {
        @include fontstyle (700, 16px, 1.75);
        margin-bottom: 60px;
    }
};

.products-list__btn {
    display: block;
    border: none;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    margin: 0 auto;
    background-position: center;
    background-size: contain;
    background-image: url(../images/products/arrow-right.png);
    @include mq(retina) {
        background-image: url(../images/products/arrow-right@2x.png);
    }
};